<template>
    <div class="main-cont">
        <div class="container">
            <div class="shop-container">
                <div class="shop">
                    <p>Маркетплейс</p>
                    <div class="words">
                        <div v-for="(word, wordIndex) in words" :key="wordIndex" class="word"
                            v-show="currentWordIndex === wordIndex">
                            <span v-for="(letter, letterIndex) in visibleLetters[wordIndex]" :key="letterIndex">
                                {{ letter }}
                            </span>
                        </div>
                    </div>
                    <p class="promo">
                        Подготовьтесь к весне уже сегодня, заплатив <br>
                        $0 за доставку при регистрации у нас…
                    </p>
                    <a href="/shop">В МАГАЗИН <img src="../assets/img/buttonLink.jpg" alt=""></a>
                </div>
            </div>
            <div class="favourites-container">
                <p>ЛЮБИМОЕ</p>
                <div class="heading">
                    <span></span>
                    <p>САМЫЕ ПРОДАВАЕМЫЕ ПРОДУКТЫ</p>
                    <span></span>
                </div>
                <div class="catalog" id="favourites">
                    <ProductCard v-for="product in catalogProducts.slice(0, visibleCount)" :key="product.id"
                        :image="product.image" :type="product.type" :name="product.name"
                        :starsProduct="product.starsProduct" :reviews="product.reviews" :starsUser="product.starsUser"
                        :price="product.price" :max-items="product.maxItems" :article="product.article"
                        :detail="product.detail" :id="product.id" />
                </div>
                <button v-if="catalogProducts.length > visibleCount" @click="loadMore" class="load-more">Load more</button>
                <button v-if="visibleCount > 8" @click="loadLess" class="load-less">Load less</button>
            </div>
            <div class="popular-container">
                <div class="heading">
                    <span></span>
                    <p>Популярные</p>
                    <span></span>
                </div>
                <div class="catalog" id="popular">
                    <ProductCard v-for="product in catalogProducts.slice(0, visibleCount)" :key="product.id"
                        :image="product.image" :type="product.type" :name="product.name"
                        :starsProduct="product.starsProduct" :reviews="product.reviews" :starsUser="product.starsUser"
                        :price="product.price" :max-items="product.maxItems" :article="product.article"
                        :detail="product.detail" :id="product.id" />
                </div>
                <button v-if="catalogProducts.length > visibleCount" @click="loadMore" class="load-more">Load more</button>
                <button v-if="visibleCount > 8" @click="loadLess">Load less</button>
            </div>
            <div class="start-day">
                <div class="container">
                    <p>Начните свой день с вкусных <br> органических овощей</p>
                    <a href="/shop">В МАГАЗИН <img src="../assets/img/buttonLink.jpg" alt=""></a>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import ProductCard from '@/components/productCard.vue';
import ProductInfo from '@/components/productInfo.vue';

export default {
    components: {
        ProductCard,
    },
    data() {
        return {
            catalogProducts: [],
            visibleCount: 8,
            words: [
                ['л', 'е', 'г', 'ч', 'е'],
                ['л', 'е', 'г', 'к', 'о', 'г', 'о'],
                ['з', 'д', 'о', 'р', 'о', 'в', 'ы', 'й'],
            ],
            isVisible: [],
            delayBetweenLetters: 200,
            delayBetweenWords: 1000,
            currentWordIndex: 0,
        };
    },
    computed: {
        visibleLetters() {
            return this.words.map((word, wordIndex) => {
                if (!this.isVisible[wordIndex]) return [];
                return word.filter((_, letterIndex) => this.isVisible[wordIndex][letterIndex]);
            });
        },
    },
    mounted() {
        this.startTypingEffect();
        this.catalogProducts = ProductInfo.data().catalogProducts;
    },
    methods: {
        loadMore() {
            this.visibleCount += 8;
        },
        loadLess() {
            if (this.visibleCount > 8) {
                this.visibleCount = 8;
            }
        },
        startTypingEffect() {
            this.isVisible = this.words.map(word => Array(word.length).fill(false));
            this.typeWord(this.currentWordIndex, this.words[this.currentWordIndex]);
        },
        typeWord(wordIndex, word) {
            word.forEach((_, letterIndex) => {
                setTimeout(() => {
                    this.isVisible[wordIndex][letterIndex] = true;
                    if (letterIndex === word.length - 1) {
                        setTimeout(() => {
                            this.currentWordIndex = (this.currentWordIndex + 1) % this.words.length;
                            this.resetVisibility();
                            this.typeWord(this.currentWordIndex, this.words[this.currentWordIndex]);
                        }, this.delayBetweenWords);
                    }
                }, this.delayBetweenLetters * letterIndex);
            });
        },
        resetVisibility() {
            this.isVisible[this.currentWordIndex] = Array(this.words[this.currentWordIndex].length).fill(false);
        },
    },
};
</script>
