<template>
    <div class="product-card">
        <button class="toggleWishlist" @click="wishlistToggle">
            <img src="../assets/img/inst.png" alt="">
        </button>
        <div class="image">
            <img :src="imageSrc" alt="">
            <button class="view-button" @click="openPopup">QUICK VIEW</button>
        </div>
        <p class="type">{{ type }}</p>
        <p class="name">{{ name }}</p>
        <div class="stars">
            <img v-for="star in starArrayProduct" :key="star" src="../assets/img/star.png" alt="">
        </div>
        <p class="price">{{ price }}₽</p>
        <div class="calc">
            <button @click="decreaseQuantity">-</button>
            <input type="number" v-model="quantity" min="1" :max="maxItems" @input="checkQuantity">
            <button @click="increaseQuantity">+</button>
            <button class="add-to-cart">
                <img src="../assets/img/cart.png" alt="">
            </button>
        </div>

        <QuickView v-if="showPopup" 
                   v-bind="productDetails" 
                   @update-quantity="updateQuantity" 
                   @close-popup="showPopup = false" 
                   @wishlist="wishlistToggle()" />
    </div>
</template>

<script>
import QuickView from '../components/quickView.vue';

export default {
    components: { QuickView },
    props: {
        image: { type: String, required: true },
        type: { type: String, required: true },
        name: { type: String, required: true },
        starsUser:  { type: Number, required: true },
        price: { type: Number, required: true },
        maxItems: { type: Number, required: true },
        reviews: { type: Number, required: true },
        starsProduct: { type: Number, required: true },
        article: { type: String, required: true },
        id: { type: Number, required: true },
        detail: { type: String, required: true },
    },
    data() {
        return {
            showPopup: false,
            quantity: 1,
            wishlist: false,
        };
    },
    methods: {
        decreaseQuantity() {
            if (this.quantity > 1) this.quantity--;
            this.$emit('update-quantity', this.quantity);
        },
        increaseQuantity() {
            if (this.quantity < this.maxItems) this.quantity++;
            this.$emit('update-quantity', this.quantity);
        },
        checkQuantity() {
            this.quantity = Math.max(1, Math.min(this.quantity, this.maxItems));
        },
        openPopup() {
            this.showPopup = true;
        },
        wishlistToggle() {
            this.wishlist = !this.wishlist;
            this.$emit('wishlist', this.wishlist);
        },
    },
    computed: {
        imageSrc() {
            return require(`@/assets/img/${this.image}`);
        },
        starArrayProduct() {
            return Array.from({ length: this.starsProduct }, (_, i) => i + 1);
        },
        productDetails() {
            return {
                image: this.imageSrc,
                name: this.name,
                price: this.price,
                reviews: this.reviews,
                article: this.article,
                id: this.id,
                detail: this.detail,
                starsUser:  this.starsUser ,
                starsProduct: this.starsProduct,
                maxItems: this.maxItems,
                quantity: this.quantity,
                wishlist: this.wishlist,
            };
        },
    },
};
</script>
